<template>
  <div style="flex:1;display:flex;flex-direction:column">
    <CommentList v-bind="{objectId: catalogId, workspaceId, members}" ref="commentList" />
    <CommentAdd v-bind="{catalogId, workspaceId, modelId, members}"
      @on-add-comment="$refs.commentList.fetchCommentList('add')" />
  </div>
</template>

<script>
import {
  fetchAtableList,
} from '@/api/comment';
import { getContextUser } from '../../../../loaders/context';
import CommentList from './CommentList';
import CommentAdd from './CommentAdd';

export default {
  dialogify: {
    props: {
      width: '520px',
      height: '620px',
      flex: 'column',
      // showClose: false,
      modal: false,
    },
  },
  data() {
    return {
      members: [],
    };
  },
  props: {
    modelId: {
      default() {
        return this.$route.params.modelId;
      },
    },
    catalogId: {
      default() {
        return this.$route.query.catalogId;
      },
    },
    workspaceId: {
      default() {
        return this.$route.query.workspaceId;
      },
    },
  },
  components: {
    CommentList,
    CommentAdd,
  },
  async created() {
    const { data } = await fetchAtableList(this.workspaceId);
    this.members = data.filter(e => e.id !== this.userId);
  },
  computed: {
    userId() {
      return getContextUser().id;
    },
  },
};
</script>
